body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

nav {
    font-size: 30px;
    padding: 20px;
    background-color: #00ddfc;
    color: white;
  }

  .title {
    float: left;
  }
  
  .header {
    float: left;
    margin-left: 30%;
  }
  .score {
    float: right;
  }
.jumbotron {
    background-image: url(/REACT-Clicky-Game/static/media/background.1baf6ab9.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
    color: white;
    margin-bottom: 0;
    padding-top: 300px;
  }
  
  .container {
    color: #10465e;
    background-color: rgba(255, 255, 255, 0.616);
  }
.char-card {
    display: inline-block;
    margin: 10px;
}

.charImg {
    width: 200px;
    border: 3px solid black;
}

.charImg:hover {
    cursor: pointer;
}
.container {
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding-top: 25px;
  padding-bottom: 25px;
  justify-content: center;
  align-content: flex-start;
}

.small-detail {
  font-size: 40px;
  text-align: center;
}

#root {
  background-color: #7dbeda;
}
